.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.ShortBox {
  border: 2px solid black;
  width: 1465px;
  height: 520px;
  margin-top: 40px;
  margin-left: 20px;
  display: flex;
}

.Welcome_text {
  margin-top: 3px;
  font-size: 45px;
  color: currentcolor;
  margin-bottom: 10px;
  border-bottom: 2px solid;
  margin-left: 88px;
  margin-right: 88px;
}